body {
    margin: 0;
    padding: 0;
    font-family: 'Monserat', sans-serif;
    background-image: linear-gradient(to right, #236e5d 0%, #133869 100%);
}

.card {
    background: rgb(143, 212, 189);
    border-radius: .5em;
    padding: 1em;
    margin: .5em;
    width: 17em;
    height: 25em;
    box-shadow: 3px 4px 8px rgb(0, 0, 0, 0.5);
    transition: .5s;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.card:hover {
    transform: scale(1.05);
}

.grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(20em, 1fr));
    justify-items: center;
    gap: .5em;
}

.searchbox-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1em;
}

.searchbox {
    padding: 1em;
    text-align: center;
    background-color: rgb(143, 212, 189);
    border: 3px solid #199cb1;
}

.title {
    text-align: center;
}